// 
export default class AccountManager {
  constructor() {
    /*
     * 
     * 0: 
     * 1:  - 
     * 2:  - 
     * 3:  - ios
     * 4:  - 
     * 5:  - 
     * 6:  -  1: 2:QQ 3: 4:
     */
    this._type = {
      GUEST: 1,
      PLATFORM: 2,
      IOS: 3,
      ANDROID: 4,
      WECHAT: 5,
      CHANNEL: 6
    };
  }

  // 
  get type() {
    return this._type;
  }

  // 
  checkLogged() {
    let logged = false;
    const code = coingame.tools.getQueryParams("code");

    if (code !== "") {
      logged = true;

      localStorage.setItem("code", code);

      // url
      window.history.replaceState(null, null, "/");
    } else {
      localStorage.removeItem("code");

      // token
      const token =
        localStorage.getItem("token") || coingame.cookie.get("token");

      if (token === null && !location.search.includes("preflight")) {
        const href =
          coingame.sys.config.loginUrl || localStorage.getItem("loginUrl");

        if (typeof href === "string") {
          // return false;
          location.replace(href + "&preflight=true");
        }
      } else {
        logged = true;
      }
    }

    return logged;
  }

  /*
   * 
   * @param {string} type 
   * @param {object} data 
   * @param {function} success 
   * @param {function} error 
   * @param {function} complete 
   */
  login({ type = 0, data = {}, success, error, complete } = {}) {
    switch (type) {
      case this._type.GUEST:
        break;
      case this._type.PLATFORM:
        break;
      case this._type.IOS:
        break;
      case this._type.ANDROID:
        break;
      case this._type.WECHAT:
        break;
      case this._type.CHANNEL:
        break;
      default:
        const href =
          coingame.sys.config.loginUrl || localStorage.getItem("loginUrl");

        if (typeof href === "string") {
          location.href = href;
        }
    }
  }

  /*
   * 
   * @param {string} type 
   * @param {object} data 
   * @param {function} success 
   * @param {function} error 
   * @param {function} complete 
   */
  logout({ type = "", data = {}, success, error, complete } = {}) {
    switch (type) {
      case this._type.GUEST:
        break;
      case this._type.PLATFORM:
        break;
      case this._type.IOS:
        break;
      case this._type.ANDROID:
        break;
      case this._type.WECHAT:
        break;
      case this._type.CHANNEL:
        break;
      default:
        const href =
          coingame.sys.config.logoutUrl || localStorage.getItem("logoutUrl");

        if (typeof href === "string") {
          localStorage.clear();
          location.href = href;
        }
    }
  }

  // 
  home() {
    const href = coingame.sys.config.homeUrl || localStorage.getItem("homeUrl");
    location.href = href;
  }

  // 
  app() {
    const href =
      coingame.sys.config.androidDownloadUrl ||
      localStorage.getItem("androidDownloadUrl");
    location.href = href;
  }
}
