// http 
export default class Request {
    constructor() {
        this._xhr = null;
        this._url = null;
        this._method = null;
        this._data = null;
        // 
        this._time = 5000;

        // 
        this._count = 0;
        // 
        this._max = 3;

        // 
        this._success = null;
        this._fail = null;
        this._complete = null;

        // Promise 
        this._resolve = null;
        this._reject = null;
    }

    /*
     * 
     * @param (string) url 
     * @param (string) method  GET | POST
     * @param (object) data 
     * @param (function) success 
     * @param (function) fail 
     * @param (function) complete 
     * @param (function) resolve Promise resolve
     * @param (function) reject Promise reject
     */
    send({
        url,
        method = 'GET',
        data,
        success,
        fail,
        complete,
        resolve = null,
        reject = null
    }) {
        this._success = success;
        this._fail = fail;
        this._complete = complete;
        this._resolve = resolve;
        this._reject = reject;

        const xhr = new XMLHttpRequest();
        let formartUrl = url;
        let formartData = null;

        if (typeof data === 'object') {
            formartData = Object.keys(data).map(function(key) {
                const str = typeof data[key] === 'object' ? JSON.stringify(data[key]) : data[key];

                return encodeURIComponent(key) + '=' + encodeURIComponent(str);
            }).join('&');
        }

        if (method === 'GET') {
            if (formartData !== null) {
                if (formartUrl.includes('?')) {
                    formartUrl += '&' + formartData;
                } else {
                    formartUrl = formartUrl + '?' + formartData;
                }
            }

            xhr.open(method, formartUrl, true);
        } else if (method === 'POST') {
            xhr.open(method, formartUrl, true);

            xhr.setRequestHeader('X-Requested-With', 'XMLHttpRequest');
            xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
        }

        xhr.timeout = this._time;

        this._initEvent(xhr);

        this._xhr = xhr;
        this._url = formartUrl;
        this._method = method;
        this._data = formartData;

        this._send();
    }

    // 
    abort() {
        this._xhr.abort();
    }

    /*
     * 
     * @param (object) xhr xhr
     */ 
    _initEvent(xhr) {
        // 
        xhr.onload = () => {
            this._load();
        };

        // 
        xhr.onloadend = () => {
            this._loadend();
        };

        // 
        xhr.onerror = () => {
            this._error();
        };

        // 
        xhr.ontimeout = () => {
            this._timeout();
        };

        // 
        xhr.onabort = () => {
            this._abort();
        };
    }

    // 
    _send() {
        // xhr.send(data)Uncaught NetworkError: Failed to execute 'send' on 'XMLHttpRequest'
        try {
            this._xhr.send(this._data)
        } catch (err) {
            if (this._reject !== null) {
                this._reject({
                    status: 'networkError',
                    statusText: 'sendRequest networkError'
                });
            }
        }
    }

    // 
    _retry() {
        this._count++;

        const xhr = new XMLHttpRequest();

        xhr.timeout = this._time;

        xhr.open(this._method, this._url, true);

        if (this._method === 'POST') {
            xhr.setRequestHeader('X-Requested-With', 'XMLHttpRequest');
            xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
        }

        this._initEvent(xhr);

        this.xhr = xhr;

        this._send();
    }

    // 
    _load() {
        const xhr = this._xhr;

        if ((xhr.status >= 200 && xhr.status < 300) || xhr.status == 304) {
            if (xhr.responseText.trim() === '') {
                if (this._reject !== null) {
                    this._reject({
                        status: xhr.status,
                        statusText: xhr.statusText
                    });
                }

                return;
            }

            const data = JSON.parse(xhr.responseText);

            if (typeof this._success === 'function') {
                this._success(data);
            }

            if (this._resolve !== null) {
                this._resolve(data);
            }
        } else {
            if (this._reject !== null) {
                this._reject({
                    status: xhr.status,
                    statusText: xhr.statusText
                });
            }
        }
    }

    // 
    _loadend() {
        if (typeof this._complete === 'function') {
            this._complete();
        }
    }

    // 
    _error() {
        if (typeof this._fail === 'function') {
            this._fail();
        }

        if (this._reject !== null) {
            this._reject({
                status: this._xhr.status,
                statusText: this._xhr.statusText
            });
        }
    }

    // 
    _timeout() {
        if (this._count < this._max) {
            this._retry();
        } else {
            if (this._reject !== null) {
                this._reject({
                    status: 'timeout',
                    statusText: 'sendRequest timeout'
                });
            }
        }
    }

    // 
    _abort() {
        if (this._reject !== null) {
            this._reject({
                status: 'abort',
                statusText: 'sendRequest abort'
            });
        }
    }
}