// cookie
class Cookie {
    constructor() {}

    /**
     * cookie
     * @param {string} key cookie
     */
    get(key) {
        const arr = document.cookie.match(
            new RegExp('(^| )' + key + '=([^;]*)(;|$)'),
        );

        if (arr != null) {
            return unescape(arr[2]);
        }

        return null;
    }

    /**
     * cookie
     * @param {string} key cookie
     * @param {string} value cookie
     * @param {number} expires ()
     */
    set(key, value, expires = 1) {
        const Days = expires || 1;
        const exp = new Date();

        exp.setTime(exp.getTime() + Days * 24 * 60 * 60 * 1000);
        document.cookie = key + '=' + escape(value) + ';expires=' + exp.toUTCString();
    }

    /**
     * cookie
     * @param {string} key cookie
     */
    remove(key) {
        const exp = new Date();
        exp.setTime(exp.getTime() - 1);

        const cval = this.get(key);
        if (cval != null) {
            document.cookie = key + '=' + cval + ';expires=' + exp.toUTCString();
        }
    }
}

const cookie = new Cookie();

export default cookie;