//  - HTTP
import Request from "../utils/Request.js";
//  - Cookie
import cookie from "../utils/cookie.js";
//  - 
import * as tools from "../utils/tools.js";

// 
export default class SystemtManager {
  constructor() {
    // 
    this._path = "/getDomain";
    // 
    this._request = null;
    // 
    this._config = {};
    // 
    this._browser = {};
  }

  /*
   * 
   * @return {object} result
   */
  get config() {
    return this._config;
  }

  /*
   * 
   * @return {object} result
   */
  get browser() {
    return this._browser;
  }

  /*
   * 
   * @param {string} url 
   * @param {string} origin 
   * @param {function} callback 
   */
  init(data, override) {
    this.getLanguage();

    this.getChannel();

    this.getConfig(data, override);

    this.getBrowser();
  }

  // 
  getBrowser() {
    const ua = navigator.userAgent;
    const app = navigator.appVersion;
    const browser = {
      trident: ua.includes("Trident"), //IE
      presto: ua.includes("Presto"), //opera
      webKit: ua.includes("AppleWebKit"), //
      gecko: ua.includes("Gecko") && !ua.includes("KHTML"), //
      mobile: !!ua.match(/AppleWebKit.*Mobile.*/), //
      ios: !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios
      android: ua.includes("Android") || ua.includes("Adr"), //android
      iPhone: ua.includes("iPhone"), //iPhoneQQHD
      iPad: ua.includes("iPad"), //iPad
      webApp: !ua.includes("Safari"), //web
      wechat: ua.includes("MicroMessenger"), //
      qq: ua.match(/\sQQ/i) === "qq" //QQ
    };

    // 
    this._browser = browser;
  }

  // 
  getLanguage() {
    // 
    let lang =
      tools.getQueryParams("l") ||
      tools.getQueryParams("lang") ||
      cookie.get("l") ||
      cookie.get("lang") ||
      localStorage.getItem("l") ||
      localStorage.getItem("lang");

    if (typeof lang !== "string" || lang === "") {
      lang = (navigator.browserLanguage || navigator.language).toLowerCase();

      if (lang === "zh-cn") {
        lang = "zh";
      }
    }

    localStorage.setItem("lang", lang);

    Object.assign(this._config, { lang });
  }

  // 
  getChannel() {
    const channel =
      tools.getQueryParams("channel") ||
      cookie.get("channel") ||
      localStorage.getItem("channel") ||
      "";

    localStorage.setItem("channel", channel);

    Object.assign(this._config, { channel });
  }

  /*
   * 
   * @param {string} url 
   * @param {string} origin 
   * @param {function} success 
   * @param {function} error 
   */
  getConfig(
    { url = "", origin = "", host = "", success, error },
    override = {}
  ) {
    if (url === "") {
      if (origin === "") {
        origin = location.origin;
      }

      url = origin + this._path;
    }

    if (this._request !== null) {
      this._request.abort();

      this._request = null;
    }

    const request = new Request();

    request.send({
      url: url,
      data: {
        host: host || location.host
      },
      success: res => {
        const { code, data } = res;
        if (code === 200) {
          console.log(override);
          this._save({
            ...data,
            ...override
          });

          typeof success === "function" && success(data);
        }
      },
      error: () => {
        typeof error === "function" && error();
      },
      complete: res => {
        this._request = null;
      }
    });

    this._request = request;
  }

  /*
   * 
   * @param {object} data 
   * domain 
   * domainRecharge 
   * api api
   * cdn 
   * sso sso
   * channelRecharge 
   * loginUrl 
   * logoutUrl 
   * androidDownloadUrl android
   * rechargeUrlPc pc
   * rechargeUrlMobile 
   * rechargeUrlChannel 
   * clientId ID
   */
  _save(data) {
    const {
      domain,
      domainRecharge,
      api,
      cdn,
      sso,
      channelRecharge,
      homeUrl,
      loginUrl,
      logoutUrl,
      serverApi,
      clientId
    } = data;

    // 
    data.domain = `${location.protocol}//${domain}`;
    data.domainRecharge = `${location.protocol}//${domainRecharge}`;
    data.api = `${location.protocol}//${api}`;
    data.cdn = `${location.protocol}//${cdn}`;
    data.sso = `${location.protocol}//${sso}`;
    data.channelRecharge = `${location.protocol}//${channelRecharge}`;
    data.homeUrl = `${location.protocol}//${homeUrl}`;
    data.serverApi = `${location.protocol}//${serverApi}`;

    data.loginUrl = data.sso + loginUrl;
    data.logoutUrl = data.sso + logoutUrl;
    //  &  code
    const redirectUrl = encodeURIComponent(location.origin);

    data.loginUrl = data.loginUrl
      .replace("{clientId}", clientId)
      .replace("{redirectUrl}", redirectUrl);
    data.logoutUrl = data.logoutUrl.replace("{redirectUrl}", redirectUrl);
    // 
    for (let key in data) {
      localStorage.setItem(key, data[key]);
    }

    Object.assign(this._config, data);
  }
}
