// 
export default class PayManager {
  constructor() {}

  /*
   * @param {string} type  0:, 1:
   * @param {string} channel 
   * @param {object} data 
   * @param {function} success 
   * @param {function} error 
   * @param {function} complete 
   */
  request({ type, channel, data, success, error, complete }) {
    if (type === "0") {
      // 
      this._withdraw({
        channel,
        data,
        success,
        error,
        complete
      });
    } else if (type === "1") {
      // 
      this._deposit({
        channel,
        data,
        success,
        error,
        complete
      });
    }
  }

  /*
   * 
   * @param {string} channel 
   * @param {object} data 
   * @param {function} success 
   * @param {function} error 
   * @param {function} complete 
   */
  _deposit({ channel, data, success, error, complete }) {
    switch (channel) {
      case "0": // android()
        break;
      case "1": // android()
        break;
      case "2": // ios
        break;
      case "3": // H5()
        break;
      case "4": // H5()
        break;
      default:
        this._coingameDeposit(data);
    }
  }

  /*
   * 
   * @param {string} currency 
   * @param {string} lang 
   * @param {string} operate 
   */
  _coingameDeposit({ currency, lang, operate = "deposit" }) {
    const {
      domainRecharge,
      rechargeUrlMobile,
      rechargeUrlPc
    } = coingame.sys.config;
    const { mobile } = coingame.sys.browser;

    let href = domainRecharge;

    href += mobile ? rechargeUrlMobile : rechargeUrlPc;

    href = href
      .replace("{currency}", currency)
      .replace("{lang}", lang)
      .replace("{operate}", operate);

    location.href = href;
  }

  /*
   * 
   * @param {string} channel 
   * @param {object} data 
   * @param {function} success 
   * @param {function} error 
   * @param {function} complete 
   */
  _withdraw({ channel, data, success, error, complete }) {
    switch (channel) {
      case "0": // android()
        break;
      case "1": // android()
        break;
      case "2": // ios
        break;
      case "3": // H5()
        break;
      case "4": // H5()
        break;
      default:
        this._coingameWithdraw(data);
    }
  }

  /*
   * 
   * @param {string} currency 
   * @param {string} lang 
   * @param {string} operate 
   */
  _coingameWithdraw({ currency, lang, operate = "withdraw" }) {
    const {
      domainRecharge,
      rechargeUrlMobile,
      rechargeUrlPc
    } = coingame.sys.config;
    const { mobile } = coingame.sys.browser;

    let href = domainRecharge;

    href += mobile ? rechargeUrlMobile : rechargeUrlPc;

    href = href
      .replace("{currency}", currency)
      .replace("{lang}", lang)
      .replace("{operate}", operate);
    location.href = href;
  }
}
